
body {
	font-family: "Poppins";
  }

  .Homebody{
	background: url(https://svgshare.com/i/rZc.svg) center 34% repeat-x;
	background-position: center 5%;
  }

.Container{
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.Preview {
	justify-content: space-between;
	display: flex;
	align-items: center;
}

.Text_preview {
	width: 50%;
}

.Text_container{
	margin-top: 70px;
	text-align: center;
	width: 80%;
}

.Canvas_preview{
	background-color: #EADCF8;
	align-items: center;
	display: flex;
	width: 80%;
	padding-top: 20px;
	padding-bottom: 20px;
	border-radius: 10px;
	flex-wrap: wrap;
}

.Canvas_container{
	width: 60%;
	justify-content: space-around;
	display: flex;
	align-items: baseline;
	padding-top: 20px;
	padding-bottom: 20px;
	flex-wrap: wrap;

}

.Container_canvas{
	display: flex;
	justify-content : space-evenly;
	flex-direction: row;
	align-items: center;
	width: 100%;

}

.Explore_canvas{
	display: flex;
	justify-content : space-evenly;
	flex-direction: row;
	align-items: center;
	width: 100%;
}

.export_list{
}

::marker {
  color: #000;
}

.List_features{
	margin-top: 20px;
	background-color: #EADCF8;
	display: flex;
	justify-content : space-evenly;
	align-items: baseline;
	flex-direction: row;
	width: 100%;
	padding-top: 60px;
	padding-bottom: 60px;
	margin-bottom: 40px;
	flex-wrap: wrap;
	border-radius: 10px;
}