.dash_container{
    position: absolute;
    top: 70px;
    top: 70px;
    left: 250px;
    min-height: max-content;
    width: calc(100% - 250px);
    background-color: var(--body-color);
    transition: var(--tran-05);
    
}
.dash_container .Dash_text{
    font-size: 30px;
    font-weight: 500;
    color: var(--text-color);
    padding: 12px 60px;
}

.sidebar.close ~ .dash_container{
    left: 78px;
    /* height: 100%; */
    width: calc(100% - 78px);
}
body.dark .dash_container .text{
    color: var(--text-color);
}

.dash_card{
    width: 100%;
    /* height: 400px; */
    background-color: #fff;
    display: flex;
    flex-direction: row;
    /* justify-content: space-between; */
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
    border-radius: 10px;
}

.dash_card_orders{
    width: 100%;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    /* justify-content: space-between; */
    /* align-items: center; */
    box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
    border-radius: 10px;
}

.tess{
    box-shadow: rgba(0, 0, 0, 0.08) 0px 4px 12px;
}